@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir-Book.ttf);
}

@font-face {
  font-family: Avenir;
  font-weight: bold;
  src: url(fonts/Avenir-Heavy.ttf);
}

.searchBar {
  margin: 20px 0;
}

.largeSearchBar {
  padding-left: 62px;
  height: 70px;
  font-size: 16px;
  line-height: 22px;
}

.littleSearchBar {
  padding-left: 33px;
  height: 42px;
  font-size: 14px;
  line-height: 19px;
}

.largeSearchBar,
.littleSearchBar {
  width: 100%;
  background: #fff;
  border: 1px solid #e1e7ed;
  box-sizing: border-box;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  color: #3d4e61;
  font-family: Avenir, sans-serif;
}

.largeSearchBar:hover,
.littleSearchBar:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.largeSearchBar:focus,
.littleSearchBar:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: bold;
}

.vectorLarge {
  left: 35px;
  top: 45px;
}

.vectorLittle {
  height: 11px;
  left: 21px;
  top: 125px;
}

.vectorLittle,
.vectorLarge {
  position: absolute;
  box-sizing: border-box;
}
